<template>
  <div>
    <div class="content__top">
      <Breadcrumbs :crumbs="crumbs"
                   @selectedCrumb="goToPage($event)"
      />
    </div>

    <section class="cart">
      <ValidationObserver v-slot="{handleSubmit}">
        <form class="cart__form form"
              @submit.prevent="handleSubmit(createOrder)"
        >
          <div class="cart__field">
            <div class="cart__data">
              <BaseFieldText title=""
                             v-model="dataForm.name"
                             rules="required"
                             placeholder="   "
              />

              <BaseFieldText title=""
                             v-model="dataForm.address"
                             rules="required"
                             placeholder="  "
              />

              <BaseFieldText title="Email"
                             v-model="dataForm.email"
                             rules="required|email"
                             placeholder="  email"
              />

              <BaseFieldText title=""
                             v-mask="'+7 (###)-###-##-##'"
                             v-model="dataForm.phone"
                             rules="required|min:18"
                             placeholder="  "
              />

              <BaseFieldTextArea title="  "
                                 v-model="dataForm.comment"
                                 placeholder=" "
              />
            </div>

            <Loader v-if="isLoading"/>
            <div v-else
                 class="cart__options"
            >
              <h3 class="cart__title"></h3>
              <div v-if="errors.deliveryError"
                   class="cart__options options"
              >
                {{ errors.deliveryError }}
              </div>
              <ul v-else
                  class="cart__options options"
              >
                <li class="options__item"
                    v-for="delivery in deliveries"
                    :key="delivery.id"
                >
                  <label class="options__label">
                    <input class="options__radio sr-only"
                           type="radio"
                           name="delivery"
                           :value="delivery"
                           v-model="selectedDelivery"
                    >
                    <span class="options__value">
                    {{ delivery.title }}
                    <b v-if="delivery.price == 0"></b>
                    <b v-else>{{ delivery.price | numberFormat }} </b>
                  </span>
                  </label>
                </li>
              </ul>

              <h3 class="cart__title"></h3>
              <div v-if="errors.deliveryError">  /  </div>
              <ul class="cart__options options">
                <li class="options__item"
                    v-for="payment in payments"
                    :key="payment.id"
                >
                  <label class="options__label">
                    <input class="options__radio sr-only"
                           type="radio"
                           name="pay"
                           :value="payment"
                           v-model="selectedPayment"
                    >
                    <span class="options__value">
                    {{ payment.title }}
                  </span>
                  </label>
                </li>

              </ul>
            </div>
          </div>

          <BasketInfoOrder :delivery="selectedDelivery">
            <button class="cart__button button button--primery"
                    type="submit"
            >
               
            </button>
          </BasketInfoOrder>

          <div class="cart__error form__error-block"
               v-if="errors.requestOrderError"
          >
            <h4>  !</h4>
            <p>
                .      .
            </p>
            <RequestError
              @load="createOrder"
            />
          </div>
        </form>
      </ValidationObserver>
    </section>
  </div>

</template>

<script>
import instance from '@/axiosConfig';

import Breadcrumbs from '@/components/UI/Breadcrumbs';
import BaseFormField from '@/components/Form/BaseFormField';
import BaseFieldText from '@/components/Form/BaseFieldText';
import BaseFieldTextArea from '@/components/Form/BaseFieldTextArea';
import BasketInfoOrder from '@/components/Basket/BasketInfoOrder';
import Loader from '@/components/UI/Loader/Loader';
import RequestError from '@/components/UI/RequestError';

import numberFormat from '@/helpers/numberFormat';

import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { email, min, required } from 'vee-validate/dist/rules';
import { mask } from 'vue-the-mask';

import { mapMutations } from 'vuex';

extend('required', {
  ...required,
  message: '    '
});

extend('min', {
  ...min,
  message: ' '
});

extend('email', {
  ...email,
  message: ' email'
});

export default {
  components: {
    Breadcrumbs,
    BaseFormField,
    BaseFieldText,
    BaseFieldTextArea,
    BasketInfoOrder,
    Loader,
    RequestError,
    ValidationProvider,
    ValidationObserver
  },
  directives: { mask },
  data() {
    return {
      crumbs: ['', ' '],
      deliveries: [],
      selectedDelivery: '',
      payments: [],
      selectedPayment: '',
      dataForm: {
        name: '',
        address: '',
        phone: '',
        email: '',
        comment: ''
      },
      errors: {
        deliveryError: '',
        paymentsError: '',
        requestOrderError: ''
      },
      isLoading: false

    };
  },
  mounted() {
    this.getDeliveries();
  },
  methods: {
    ...mapMutations(['updateBasket']),
    goToPage(event) {
      this.$router.push({ name: 'Basket' });
    },
    getDeliveries() {
      this.isLoading = true;
      instance.get('deliveries')
        .then(res => {
          this.isLoading = false;
          this.deliveries = res.data;
          this.selectedDelivery = res.data[0];
        })
        .catch(e => {
          this.isLoading = false;
          this.errors.deliveryError = ' ,     .' +
            '    .         :)';
        });
    },
    getPayments(deliveryId) {
      this.isLoading = true;
      instance.get('payments', {
        params: {
          deliveryTypeId: deliveryId
        }
      })
        .then(res => {
          this.isLoading = false;
          this.payments = res.data;
          this.selectedPayment = res.data[0];
        })
        .catch(e => {
          this.isLoading = false;
        });
    },
    createOrder() {
      this.isLoading = true;
      let dataForm = Object.assign({}, this.dataForm, {
        deliveryTypeId: this.selectedDelivery.id,
        paymentTypeId: this.selectedPayment.id
      });
      instance.post('orders', dataForm,)
        .then(res => {
          this.isLoading = false;
          this.$router.push({
            name: 'OrderInfoPage',
            params: {
              id: res.data.id
            }
          });
          this.$store.commit('updateBasket', []);
        })
        .catch(e => {
          this.errors.requestOrderError = true;
          this.isLoading = false;
        });
    }
  },
  watch: {
    selectedDelivery(val) {
      this.getPayments(val.id);
    }
  },
  filters: {
    numberFormat
  }
};
</script>

<style scoped>
.form__error-block p {
  display: inline-block;
  margin-bottom: 20px;
}
</style>
