<template>
  <div class="form__counter">
    <button type="button"
            aria-label="  "
            @click.prevent="decrementProductCount"
    >
      <svg width="12"
           height="12"
           fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <ValidationProvider v-slot="{errors, dirty}"
                        rules="integer|positive|required"
    >
      <input type="text"
             v-model.number="counter"
             name="count"
             autocomplete="off"
      />
    </ValidationProvider>

    <button type="button"
            aria-label="  "
            @click.prevent="incrementProductCount"
    >
      <svg width="12"
           height="12"
           fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
import { extend, ValidationProvider } from 'vee-validate';
import { integer, required } from 'vee-validate/dist/rules';

export default {
  props: ['productCount'],
  components: {
    ValidationProvider
  },
  data() {
    return {
      counter: ''
    };
  },
  mounted() {
    this.counter = this.productCount;
  },
  methods: {
    incrementProductCount() {
      this.counter = +this.counter + 1;
    },
    decrementProductCount() {
      if (this.counter == 1) return;
      this.counter = +this.counter - 1;
    }
  },
  watch: {
    counter(value) {
      this.$emit('update:productCount', value);

      extend('positive', value => {
        if (value > 0) {
          this.$emit('error', '');
          return true;
        }
        this.$emit('error', '    ');
      });

      extend('integer', {
        ...integer,
        message: () => {
          this.$emit('error', ' ');
        }
      });

      extend('required', {
        ...required,
        message: () => {
          this.$emit('error', ' !   ');
        }
      });
    }
  },
};
</script>

<style scoped>
input[name='count'] {
  outline: none;
}
</style>
